var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container"},[_vm._ssrNode("<article class=\"product\" data-v-6dfca62c><div class=\"left\" data-v-6dfca62c><h1 class=\"title\" data-v-6dfca62c>Caesarsalaatti</h1><span data-v-6dfca62c>4 annosta</span><span class=\"recipe-title\" data-v-6dfca62c>Ainekset:       </span><ul data-v-6dfca62c><li data-v-6dfca62c>1 pussi Varpion friseesalaattia</li><li data-v-6dfca62c>1 pussi Varpion jääsalaattia</li><li data-v-6dfca62c>1 ruukku rucolaa</li><li data-v-6dfca62c>1 ruukku sareptansinappia</li><li data-v-6dfca62c>100 g saksanpähkinää</li><li data-v-6dfca62c>1 tl vaahterasiirappia</li><li data-v-6dfca62c>2 tl voita</li><li data-v-6dfca62c>6 rouhaisua mustapippuria myllystä</li><li data-v-6dfca62c>2 viipaletta vaaleaa leipä</li><li data-v-6dfca62c>1 tl oliiviöljyä</li><li data-v-6dfca62c>20 g kovaa juustoa, esim. parmesaania, pecorinoa tai grana padanoa</li><li data-v-6dfca62c>1 kananmuna</li><li data-v-6dfca62c>1,5 dl rypsiöljyä</li><li data-v-6dfca62c>20 g anjoviksia</li><li data-v-6dfca62c>1 valkosipulinkynsi</li><li data-v-6dfca62c>1/2 sitruunanmehu</li><li data-v-6dfca62c>Hyppysellinen suolaa</li></ul><span class=\"recipe-title\" data-v-6dfca62c>Valmistusohjeet:<ol data-v-6dfca62c><li data-v-6dfca62c>Ensin valmista kastike: laita kapeaan ja korkeaan kulhoon kananmuna, rypsiöljy, anjovikset, sitruunanmehu, valkosipuli ja suola. Aseta sauvasekoitin astianpohjalle ja ala sekoittamaan. Nosta sauvasekoitinta todella hitaasti ylöspäin, kunnes saavut pintaan. Tee sauvasekoittimella vielä muutama sekoitus ja lopeta. Sinulla pitäisi olla nyt kaunis vaalea, melko löysä majoneesi valmiina. Voit myös valmistaa kastikkeen käyttämällä valmismajoneesia, ja sekoittamalla siihen kaikki makuaineet sauvasekoittimella.</li><li data-v-6dfca62c>Paahda saksanpähkinöitä paistinpannulla keskilämmöllä noin 5 minuuttia tai kunnes pähkinöistä alkaa irrota hyvä tuoksu. Lisää pannulle vaahterasiirappi, voi ja mustapippuri. Anna pähkinöiden karamellisoitua koko ajan sekoittaen muutama minuutti ja siirrä ne lautaselle jäähtymään. Jäähtyneinä leikkaa pähkinöitä halutessasi vähän pienemmäksi veitsellä.</li><li data-v-6dfca62c>Leikkaa leipäviipaleet kuutioiksi ja laita ne paistinpannulle oliiviöljyn ja hyppysellisen suolaa kanssa. Paahda keskilämmöllä noin viisi minuuttia ja siirrä leipäkuutiot lautaselle jäähtymään. Krutongeista ei ole tarkoituskaan saada rutikuivia.</li><li data-v-6dfca62c>Revi salaattikulhoon salaatit ja lisää myös rucola. Sekoita joukkoon runsaasti valmistamaasi kastiketta ja sekoita. Leikkaa juustosta kuorimaveitsellä ohuita lastuja.</li><li data-v-6dfca62c>Annostele salaatti lautasille ja asettele päälle krutongit, saksanpähkinät sekä juustolastut. Tarjoile. Halutessasi voit tehdä salaatista vielä ruokaisamman lisäämällä siihen esimerkiksi kikherneitä, paistettua kanaa tai savustettua lohta.</li></ol></span></div><div class=\"right\" data-v-6dfca62c><img"+(_vm._ssrAttr("src",require("assets/img/recipes/ceasarsalaatti.jpg")))+" data-v-6dfca62c></div></article>")])}
